import React from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, Lock } from 'lucide-react';
import Button from '../../../components/atoms/Button';

const SumaPage: React.FC = () => {
  const navigate = useNavigate();
  
  const handleBack = () => {
    navigate('/dashboard');
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-emerald-50">
      {/* Header */}
      <div className="bg-white/90 backdrop-blur-sm shadow-sm border-b p-4">
        <div className="flex items-center justify-between max-w-6xl mx-auto">
          <div className="flex items-center gap-4">
            <Button
              onClick={handleBack}
              variant="ghost"
              size="sm"
              icon={ArrowLeft}
            >
              Volver
            </Button>
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 bg-green-100 rounded-xl flex items-center justify-center">
                <span className="text-2xl">➕</span>
              </div>
              <div>
                <h1 className="text-2xl font-bold text-gray-800">SUMA</h1>
                <p className="text-sm text-gray-600">Aprende a sumar números</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Locked Content */}
      <div className="max-w-4xl mx-auto p-6 flex items-center justify-center min-h-[60vh]">
        <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-12 shadow-xl text-center">
          <div className="w-20 h-20 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <Lock className="w-10 h-10 text-gray-500" />
          </div>
          <h2 className="text-2xl font-bold text-gray-800 mb-4">Módulo Bloqueado</h2>
          <p className="text-gray-600 mb-6">
            Completa los módulos anteriores para desbloquear Suma
          </p>
          <Button
            onClick={handleBack}
            variant="secondary"
          >
            Volver al Dashboard
          </Button>
        </div>
      </div>
    </div>
  );
};

export default SumaPage;